@import url(https://fonts.googleapis.com/css?family=Heebo:100,300,400,500,700&subset=hebrew);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Hee'
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}


html, body, #root {
  width: 100%;
  height: 100vh;
  min-height: 600px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.news-runner .news-item{height:75px;cursor:pointer;padding-left:10px;padding-right:10px;font-family:Heebo, sans-serif;font-size:20px;font-weight:bold;text-align:right;color:#000000;background:white;margin:auto auto 18px;display:flex;align-items:center;direction:rtl;border-radius:3px}.news-runner .news-item .news-source-img{width:50px}.news-runner .news-item .news-source-img.mako{width:66px}.news-runner .news-item .news-source-img.ynet{padding-left:8px;padding-right:8px}.news-runner .news-item .news-source-img.walla{padding-left:14px;padding-right:14px;width:35px}.news-runner .news-item .text:hover{text-decoration:underline}.news-runner .news-item .material-icons{margin-right:auto;padding-right:10px;cursor:pointer;border:none;background:transparent}.news-runner .news-item .material-icons:focus{outline:none}.news-runner .runner-wrapper{max-height:calc(100vh - 250px - 110px);min-height:calc(600px - 170px - 110px);overflow:auto;max-width:1200px;min-width:600px;margin:auto;padding-right:15px;padding-left:15px}@media only screen and (max-width: 650px), (max-height: 750px){.news-runner .runner-wrapper{max-height:calc(100vh - 170px - 110px)}}@media only screen and (max-width: 650px){.news-runner .runner-wrapper{overflow:auto;min-width:250px;padding-right:15px;padding-left:15px}.news-runner .runner-wrapper .news-item{font-size:13px;padding-right:4px;padding-left:4px}.news-runner .runner-wrapper .news-item .news-source-img{width:30px}.news-runner .runner-wrapper .news-item .news-source-img.mako{width:46px}.news-runner .runner-wrapper .news-item .news-source-img.ynet{padding-left:8px;padding-right:8px}.news-runner .runner-wrapper .news-item .news-source-img.walla{padding-left:10px;padding-right:10px;width:23px}}

.categories-view .category-btn{font-family:Heebo, sans-serif;font-size:32px;font-weight:700;color:#000c2d;cursor:pointer;display:flex;align-items:center;direction:rtl;height:60px;width:450px;padding-right:30px;border-radius:3px;background-color:#e9faf4;margin:auto auto 15px;opacity:0.4}.categories-view .category-btn:hover{opacity:0.85}.categories-view .category-btn.selected{opacity:1}.categories-view .category-btn .material-icons{margin-left:30px;font-size:35px}@media only screen and (max-width: 650px){.categories-view .category-btn{overflow:auto;width:250px;font-size:15px;height:50px;padding-right:15px}.categories-view .category-btn .material-icons{margin-left:20px;font-size:20px}}

.news-page{text-align:center;background-color:#000c2d;width:100vw;height:100vh;justify-content:center;font-family:Heebo, sans-serif;position:relative;min-height:600px}.news-page .app-header{height:250px}.news-page .app-header .date{color:white;direction:rtl;padding-top:15px}.news-page .app-header .logo{margin-top:50px;max-width:320px;margin-bottom:auto}.news-page .app-header .desc{font-family:Heebo, sans-serif;color:white;direction:rtl;font-weight:300;font-size:22px}.news-page .footer{position:absolute;bottom:0;height:110px;width:100%}.news-page .footer .footer-relative{position:relative;width:100%;height:100%;display:flex;flex-direction:row;direction:rtl;justify-content:center;align-items:flex-end}.news-page .footer .footer-relative .settings-button{color:white;padding-left:10px;padding-right:10px;padding-bottom:20px;font-family:Heebo, sans-serif;cursor:pointer}.news-page .footer .footer-relative .settings-button.selected,.news-page .footer .footer-relative .settings-button:hover{text-decoration:underline}@media only screen and (max-width: 650px), (max-height: 750px){.news-page .app-header{height:170px}.news-page .app-header .logo{max-width:180px;margin-top:40px}.news-page .app-header .desc{font-family:Heebo, sans-serif;font-size:11px}}

@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: url(/static/media/MaterialIcons-Regular.e79bfd88.eot); /* For IE6-8 */
    src: local('Material Icons'),
    local('MaterialIcons-Regular'),
    url(/static/media/MaterialIcons-Regular.570eb838.woff2) format('woff2'),
    url(/static/media/MaterialIcons-Regular.012cf6a1.woff) format('woff'),
    url(/static/media/MaterialIcons-Regular.a37b0c01.ttf) format('truetype');
}

.material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;  /* Preferred icon size */
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;

    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;

    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;

    /* Support for IE. */
    font-feature-settings: 'liga';
}

